:root {
  --color: #0b0b0b; 
  --color-hover: #D8A041;
  --color-active: #9D9D9D;
  --color-disabled: #9D9D9D5a;
  --color-disabled-hover: #9D9D9Da5;
  --border-color: #0b0b0b;
  --border-color-hover: #D8A041;
  --bordered-border-color: #D8A041;
  --bordered-border-color-hover: white;

  --border-color-disabled: #9D9D9D5a;
  --bg-color: transparent;
  --bg-color-primary: linear-gradient(
    266.19deg, rgba(255, 255, 255, 0.2) 
    11.25%, rgba(255, 255, 255, 0.0157739) 
    71.46%, rgba(0, 0, 0, 0) 76.61%
  ), #D8A041;
  --bg-color-primary-hover: linear-gradient(
    229.46deg, rgba(255, 255, 255, 0.35) 
    25.14%, rgba(255, 255, 255, 0.0276044) 
    111.72%, rgba(0, 0, 0, 0) 119.13%
  ), #D8A041;
}

.Button {
  display: block;
  font-size: 18px;
  border-radius: 5px;
  border: 1px solid var(--color-active);
  color: var(--color);
  padding: 8px;
  height: 48px;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
}

.Button:hover {
  padding-top: 7px;
  text-shadow: 0 1px 1px var(--color-disabled-hover);
  border: 1px solid var(--color-hover);
  color: var(--color-hover);
}

.wide {
  padding: 8px 16px;
}

a, .link {
  padding: 8px 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  text-decoration: none;
  display: pointer;
  color: var(--color);
}

a:hover, .link:hover {
  color: var(--color-hover);
}

a.active:hover, .link.active {
  color: var(--color-active);
}

a.disable:hover, .link.disable:hover {
  color: var(--color-disabled-hover);
}

.primary {
  padding: 8px 16px;
  border: none;
  color: white;
  background: var(--bg-color-primary);
} 

.primary:hover {
  border: none;
  color: white;
  background: var(--bg-color-primary-hover);
}

.bordered {
  padding: 24px 40px;
  border: 2px solid var(--bordered-border-color);
}

.bordered::after {
  content:"";
  position: absolute;
  top: 7px;
  left: 7px;
  right: 7px;
  bottom: 7px;
  border: 1px solid var(--bordered-border-color);
}

.bordered:hover {
  border: 2px solid var(--bordered-border-color-hover);
  background: 2px solid var(--bg-color-primary);
}

.bordered:hover::after {
  border: 1px solid var(--bordered-border-color-hover);
}

.disabled {
  touch-action: none;
  /* pointer-events: none; */
  cursor: progress;
  color: var(--color-disabled);
}
